import { ResponsiveContainer, Legend, Bar, BarChart, XAxis, Tooltip, YAxis, CartesianGrid } from 'recharts';
import "../styles/Charts.scss"
import { useSelector } from 'react-redux';
import { GroupForcastByDay } from '../functions/GroupForcastByDay';


const WindChart = (props) => {

    const data =[{time: "3:00", wind : 4, gusts : 5}, {time: "6:00", wind : 4, gusts : 6}, {time: "9:00", wind : 4, gusts : 13}, {time: "12:00", wind : 7, gusts : 17}, {time: "15:00", wind : 9, gusts : 12}, {time: "18:00", wind : 8, gusts : 10}, {time: "21:00", wind : 4, gusts : 10}]

    const currentStoreDay = state => state.currentDay
    const currentDay = useSelector(currentStoreDay)

    const forecastWeatherData = state => state.forecast;
    const cityForecastWeatherData = useSelector(forecastWeatherData);

    const currentStoreTime = state => state.currentTime;
    const storeTime = useSelector(currentStoreTime)

    const currentStoreTimezone = state => state.timezone;
    const storeTimezone = useSelector(currentStoreTimezone)

    let windChartData = [];

    if (currentDay !== null && cityForecastWeatherData !== null) {
    
        const forecastsGroupedByDay = GroupForcastByDay(cityForecastWeatherData);
        const dayForecast = forecastsGroupedByDay.find(element => element.date === currentDay)
        dayForecast.data.map(element => {
            
            let localTime = element.dt + storeTimezone + new Date(element.dt * 1000).getTimezoneOffset()*60;        
            let currentTime = new Date(localTime* 1000).toLocaleTimeString("fr-FR")
            let currentTimeHHMM = currentTime.substring(0, 5)
            windChartData.push({'time': currentTimeHHMM, 'vent moyen' : Math.round(element.wind.speed), 'rafales': Math.round(element.wind.gust)})
        });
    
    
    return (
        <div className = "chart-wind">
            <div className = "chart-wind__label">Vents (m/s)</div>
            <ResponsiveContainer width="100%" height={280}>    
            <BarChart width={730} height={270} data={windChartData} title="Vitesse des vents">
            <Legend layout="horizontal" verticalAlign="top" align="center"/>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="time" label />
            <YAxis />
            <Tooltip />

            <Bar dataKey="vent moyen" fill="#63b3ff" />
            <Bar dataKey="rafales" fill="orange" />
            </BarChart>
            </ResponsiveContainer>
        </div>)
        } else {
            return null;
        }
    

}

export default WindChart

