import { useSelector } from "react-redux";
import store from "./Store.js";
import ThreeHoursForecast from "./ThreeHoursForecast.jsx";
import "../styles/forecast.scss"
import { GroupForcastByDay } from "../functions/GroupForcastByDay.js";
import { FormatDate } from "../functions/FormatDate.js";
import DetailedForecast from "./DetailedForecast.jsx";

const CityForecast = () => { 

    const forecastWeatherData = state => state.forecast;
    const cityForecastWeatherData = useSelector(forecastWeatherData);

    const currentDay = state => state.currentDay;
    const cityCurrentDay = useSelector(currentDay);

    if (cityForecastWeatherData !== null) {

        const forecastsGroupedByDay = GroupForcastByDay(cityForecastWeatherData);
        const dayForecast = forecastsGroupedByDay.find(element => element.date === cityCurrentDay)
        
        //Creating react Elements array for each day
        
        const oneDayReactElements = dayForecast.data.map(forecast => {
            return(
            <ThreeHoursForecast forecast = {forecast}/>
            )
        })
        return (
            <div className = "forecast__root-container">
                    {/*<div className = "forecast__day-container__date">{FormatDate(new Date(dayForecast.date))}</div>*/}
                    <div className = "forecast__day-forecasts">{oneDayReactElements}</div>
                    <DetailedForecast forecast = {dayForecast}/>
            </div>

        )
    }
    else {
        return (
            <div>
                <div className = "forecast__day-forecasts"></div>
            </div>
        )
    }


}



export default CityForecast