export const GetWindDirection = (angle) => {
 
    //Finding out all distinct dates in the five days forecast
    let angleLabel;
    if (angle > 15 && angle <= 60) {
        angleLabel = "Nord-Est"
    } else if (angle > 60 && angle <= 105) {
        angleLabel ="Est"
    } else if (angle > 105 && angle <= 150) {
        angleLabel = "Sud-Est"
    } else if (angle > 150 && angle <= 195) {
        angleLabel ="Sud"
    } else if (angle > 195 && angle <= 240) {
        angleLabel ="Sud-Ouest"
    } else if (angle > 240 && angle <= 285) {
        angleLabel ="Ouest"
    } else if (angle > 285 && angle <= 330) {
        angleLabel ="Nord-Ouest"
    } else if (angle > 330 || angle <= 15) {
        angleLabel ="Nord"
    }

    return angleLabel
}