export const FormatDate = (date, dataType) => {
    const daysArray = ["Dim","Lun", "Mar", "Mer", "Jeu", "Ven", "Sam"]
    const monthsArray = ["Janvier", "Février", "Mars", "Avril", "Mai", "Juin", "Juillet", "Aout", "septembre", "octobre", "novembre", "décembre"] 
    let result;
    if (dataType == "dayOfWeek") {
        result = daysArray[new Date(date).getDay()];
    }
    else if (dataType == "dayOfMonth") {
        result = new Date(date).getDate();
    }
    else if (dataType == "month") {
        result = monthsArray[new Date(date).getMonth()];
    }
    else if (dataType == "full") {
        const dayOfWeek = daysArray[new Date(date).getDay() -1];
        const dayOfMonth = new Date(date).getDate();
        const month = monthsArray[new Date(date).getMonth()];
        result = dayOfWeek+" "+dayOfMonth+" "+month
    }

    
    return result
}