 export const GroupForcastByDay = (cityForecastWeatherData) => {
 
 //Finding out all distinct dates in the five days forecast
 const distinctDays = cityForecastWeatherData.list.map(forecastData => new Date((forecastData.dt + cityForecastWeatherData.city.timezone)*1000).toLocaleDateString("en-US"))
 .filter((value, index, self) => self.indexOf(value) === index)

 //PUSH THIS VALUE TO STATE USING DISPATCH

 //Creating Array with forecasts grouped by day.
 const forecastsGroupedByDay = [];

 //Creating a new array with the api response.
 distinctDays.map(currentDate => {
     //creating a new array to store all forecasts for one distinct day
     const currentDateData = []
     //For each forecast in the state
     cityForecastWeatherData.list.map(forecastData => {
         //If the date is the current date
         if (new Date((forecastData.dt+cityForecastWeatherData.city.timezone + new Date(forecastData.dt * 1000).getTimezoneOffset()*60) * 1000).toLocaleDateString("en-US") === currentDate) {
             //Insert data in the newly created array currentDateData
             currentDateData.push(forecastData)
         }
     })
     //Push an object composed by the current date and all related forecasts datas in forecastsGroupedByDay
     forecastsGroupedByDay.push({"date" : currentDate, "data" : currentDateData})
 })

 return forecastsGroupedByDay
}