const initialState = {currentWeather: null, forecast :null, city : "toulouse", currentDay : null, currentTime: null, timezone: null }
// Use the initialState as a default value
export default function AppReducer(state = initialState, action) {
  // The reducer normally looks at the action type field to decide what happens  
  switch (action.type) {  
      case 'api/putWeatherInState' : {
        return {
          ...state,
          currentWeather : action.payload
        }
      }
      case 'api/putForeCastInState' : {
        return {
          ...state,
          forecast : action.payload
        }
      }
      case 'api/putCityInState' : {
        return {
          ...state,
          city : action.payload
        }
      }
      case 'api/putTimezoneInState' : {
        return {
          ...state,
          timezone : action.payload
        }
      }
      case 'api/putCurrentDayInState' : {
        return {
          ...state,
          currentDay : action.payload
        }
      }
      case 'api/putCurrentTimeInState' : {
        return {
          ...state,
          currentTime : action.payload
        }
      }

    default:      
    // If this reducer doesn't recognize the action type, or doesn't      
    // care about this specific action, return the existing state unchanged 
    return state 
}}