import React, {useState} from "react";

import { useDispatch } from "react-redux";
import "../styles/CitySearchForm.scss"
import CityAutoSuggest from "./CityAutoSuggest";
import { GetMatchingCities } from "../functions/GetMatchingCities"

const CitySearchForm = () => {

    const dispatch = useDispatch()

    /*Setting up hooks for fields
    These will be used to maintain a state for the form and allow the send function to work its magic*/
    const [city, setCity] = React.useState("");

    return (

    <form className ="city-search-form" 
        onSubmit = {(e) => {
    e.preventDefault();
        if (city) {
            dispatch({ type: 'api/putCityInState', payload: city})
        }}}>
        <input placeholder = "Rechercher une ville" name = "city" value = {city} type = "text" onChange={e => {
                    setCity(e.target.value)
                    }}>
        </input>
        <button type = "submit"><i class="fab fa-searchengin"></i></button>
        <CityAutoSuggest city = {city}/>
    </form>
    )

}
export default CitySearchForm